.row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .col {
    --columns: 1;
    --size: 1;
    --offset: 15px;
    width: calc((100% / var(--columns)) * var(--size) - var(--offset) * 2);
    margin: var(--offset);
    background-color: #fff;
  }
  
  .col--1-2{
    --columns: 2;
  }
  
  .col--1-3{
    --columns: 3;
  }
  
  .col--2-3{
    --columns: 3;
    --size: 2;
  }
  
  .col--1-4{
    --columns: 4;
  }
  
  .col--1-5{
    --columns: 5;
  }